import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './User.css'
import UserList from "./UserList";
import SearchUser from "./SearchUser";


function UserCall(props) {
    const [User, setUser] = useState()
    console.log(props,'data')
    useEffect(() => {
        if(props.user === undefined){
            setUser({user : '....loading'})
            console.log(props.user)
        }else{
            setUser({user : props.user})
            console.log(props.user)
        }  
    }, [props])


    return (
        <div className='back'>
            <div className="container shadow rounded">
                <div className="row p-5">
                    <div className="col bg-light d-flex flex-column  align-items-center">
                    <SearchUser user={props.user.data} />
                    </div>
                    <div className="col bg-light d-flex flex-column justify-content-center align-items-center">
                    <h2 className='m-3'>All User</h2>
                        <UserList user={props.user.data} />
                    </div>
                </div>
            </div>
        </div>

    );
}

export default UserCall;