import React, { Component } from 'react';
import UserCall from "./Components/Users/UserCall";
import  axios from "./Utill/Axiosconfig";
export default class App extends Component {

  state={user:''}
  // [{
  //   firstName :'',
  //   LastName : '',
  //   title : '',
  //   email : '',
  //   id : ''
  // }]
    componentDidMount() {
      axios.get('/user')
      .then(data=>{
        if(data === undefined){
          this.setState({user :'loading data'});
          console.log('loading')
        }else{
          this.setState({user : data.data});
          console.log(this.state)
        }
         
      }).catch(err => err)  
     }


  render() {
    return (
      <>
       <UserCall user={this.state.user} />
      </>
    )
  }
}
